.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.hero-text-102 {
  position: absolute;
  top: 20%;
  left: 50%;
  right: 0%;
  height: 8rem !important;
  width: 8rem;
  object-fit: contain;
  background-color: white;
  transform: translate(-50%, -50%);
}

.hero-text-1002 {
  height: 8rem !important;
  width: 8rem;

  filter: drop-shadow(0px 10px 4px rgba(128, 128, 128, 0.411));
}
.storenavbar-wrap {
  margin: 0 auto;
  height: 20rem;
  z-index: 99999;
  /* opacity:0.5; */
  filter: grayscale(100%);
  background: #24273d
    url("data:image/jpeg;base64,");
}
.nav-container {
  padding-left: 15rem;
  padding-right: 15rem;
  color: white;
}
.aboutme-container {
  padding: 6rem;
  padding-left: 15rem;
  padding-right: 15rem;
  background-color: #212234;
  /* display: grid;
  
  grid-template-columns: right left   left; */
}
.services-text-line {
  width: 4%;
  height: 4px;
  margin-bottom: 3rem;

  border-bottom: 3px solid red;
}
.services-container {
  padding: 6rem;
  padding-left: 15rem;
  padding-right: 15rem;
  background-color: #24273d;
}

.aboutme-description {
  /* grid-area: left; */
  color: white;
}
.aboutme-image {
 background-color: black;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.dropbtn {
 
  color: yellow;
  padding: 16px 0px;
  font-size: 16px;
  
  cursor: pointer;
}

.dropbtn:hover, .dropbtn:focus {
   color: red;
}

.dropdown {
  /* position: relative; */
